body {
    background-color: #ffffff;
}

#table-wrapper {
    position:relative;
}
#table-scroll {
    height:150px;
    overflow:auto;  
    margin: 10px;
}
#table-wrapper table {
    width:100%;
    
}
#table-wrapper table * {
    color:black;
}
#table-wrapper table thead th .text {
    position:absolute;   
    top:-20px;
    z-index:2;
    height:20px;
    width:35%;
    border:1px solid red;
}

section {
    margin: 10px;
    padding: 5px;
    border-radius: 5px;
    background-color: #ffffff;
}

h1, h3, td, th {
    color: black;
}